export function loadingStatus(state) {
  return state.loadingStatus
}

export function user(state) {
  return state.user
}

export function toast(state) {
  return state.toast
}

export function alert(state) {
  return state.alert
}

export function assetTypes(state) {
  return state.assetTypes
}

export function enquiryError(state) {
  return state.enquiryError
}



